@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;600;700&display=swap);
:root{--color-lavender: #B8BBD3;--color-white: #fff;--color-red: #C9A3A4;--color-blue: #A7CEEF;--color-green: #ACB5AC;--color-grey: #858585;--color-light-blue: #bedcfa;--color-dark: #2E2E2E}body{margin:0;font-family:'Open Sans', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box}html,body{width:100%}.link,.outside-link{text-decoration:none;text-transform:uppercase}.link{font-weight:600;color:#FFFFFF}.outside-link{color:#858585;color:var(--color-grey)}.text{color:#FFFFFF;font-size:1.125rem}

.pages-list{display:flex}.pages-list .page-card{width:33.3333%}@media screen and (max-width: 1020px){.pages-list .page-card{width:100%}}@media screen and (max-width: 460px){.pages-list .page-card{overflow:hidden}.pages-list .page-card__img{position:absolute;height:50%;right:0}}

.main{display:flex;flex-direction:column;width:100%;min-height:100vh;padding:50px;background:#B8BBD3 url(/Neuralink/static/media/homepage-desktop.d1ce9939.png) no-repeat center/contain}.main .title{justify-self:center;padding-top:160px}.main .navigation{align-self:flex-end}.main .logo{align-self:flex-start}.main__more{align-self:flex-start;margin-top:auto;position:relative;padding:35px 0}.main__more:before{content:'';display:block;position:absolute;width:100%;background:#FFFFFF;height:2px;top:0}.main__more .button:not(:first-child){margin-left:20px}@media screen and (max-width: 1020px){.main{background-position:100% 90%}.main .title{margin-bottom:128px}}@media screen and (max-width: 816px){.main{background-image:url(/Neuralink/static/media/homepage-tablet.b2efde56.png);background-size:280px}.main .title{font-size:2.76563rem}}@media screen and (max-width: 480px){.main{background-image:url(/Neuralink/static/media/homepage-mobile.c59aaec4.png);background-position:left 75%}.main .title{font-size:2.18519rem}.main__more{flex-wrap:nowrap}.main__more .button:not(:first-child){margin-left:0}}

.title{color:#fff;margin:0;white-space:pre-line}h1.title{font-size:3.6875rem;letter-spacing:0.1125rem}h2.title{font-size:2.5625rem;font-weight:400}

.button{font-size:1.125rem;white-space:nowrap}.button[data-theme='link']{text-transform:uppercase;text-decoration:none;color:#FFFFFF}.button[data-theme='link']:hover .button__icon{transform:scale(1.25)}.button__icon{height:15px;margin-left:10px;transition:transform .25s ease-in-out}

.group{flex-wrap:wrap}.group[data-oriented='horizontal']{display:flex;flex-direction:row}.group[data-oriented='vertical']{display:flex;flex-direction:column}

.header{display:flex;flex-direction:row;justify-content:space-between;position:absolute;top:32px;padding:0 50px;width:100%;z-index:10}

.logo{display:inline-block;height:30px}.logo__img{height:100%}

.navigation{display:inline-flex}.navigation__list{margin:0;padding:0 10px}.navigation__link{list-style-type:none;padding:3px 0}.navigation__link .link{position:relative}.navigation__link .link:after{content:'';position:absolute;top:50%;right:-25px;width:15px;height:2px;background:var(--color-white);opacity:0;transition:.25s}.navigation__link .link:hover:after{opacity:1}


.page-card{display:flex;flex-direction:column;padding:50px;position:relative}.page-card .title{position:relative;padding-bottom:35px;z-index:2}.page-card .title:before{content:'';display:block;position:absolute;width:100%;background:#FFFFFF;height:2px;bottom:0}.page-card .button[data-theme='link']:after{content:'';position:absolute;top:0;bottom:0;left:0;right:0}.page-card__img{align-self:center;height:400px;max-width:100%;margin:20px 0;object-fit:contain;z-index:1}

.future{width:100%;height:100vh;box-sizing:border-box;padding:50px;background:url(/Neuralink/static/media/homepage-footer.1c319f1b.jpg) no-repeat top center/cover}.future__block{display:flex;flex-direction:column;max-width:450px;height:100%}.future__block .title{padding-bottom:35px;position:relative}.future__block .title:after{content:'';display:block;position:absolute;width:100%;background:#FFFFFF;height:2px;bottom:0}.future__block .text{width:80%;margin:35px 0}.future__block .button[data-theme='link']{margin-top:auto}

:root{--color-lavender: #B8BBD3;--color-white: #fff;--color-red: #C9A3A4;--color-blue: #A7CEEF;--color-green: #ACB5AC;--color-grey: #858585;--color-light-blue: #bedcfa;--color-dark: #2E2E2E}body{margin:0;font-family:'Open Sans', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box}html,body{width:100%}.link,.outside-link{text-decoration:none;text-transform:uppercase}.link{font-weight:600;color:#FFFFFF}.outside-link{color:#858585;color:var(--color-grey)}.text{color:#FFFFFF;font-size:1.125rem}.footer{display:flex;padding:32px;background:#fff;background:var(--color-white);flex-direction:row;justify-content:space-around}.footer *{font-size:0.875rem}.footer .copyright,.footer .link{color:#858585;color:var(--color-grey);margin:0}.footer .link:after{background:#858585;background:var(--color-grey)}.footer .outside-link{transition:.25s}.footer .outside-link:hover{color:#2E2E2E;color:var(--color-dark)}.footer .contact{justify-content:space-between;width:25%}@media screen and (max-width: 620px){.footer .contact,.footer .navigation{display:none !important}}

.contacts{display:flex;flex-direction:column}.contacts>*{margin:5px 0}

.label{position:relative;display:flex;flex-direction:row;align-items:center;padding:7px 0}.label .placeholder{color:var(--color-grey);position:absolute;-webkit-user-select:none;-ms-user-select:none;user-select:none;white-space:nowrap;transform:translate(0, -50%)}.label:after{content:'';position:absolute;display:block;width:100%;height:1px;bottom:0;background:var(--color-grey)}.input{outline:none;border:none;width:calc(100% - 16px);font-size:1rem}.input__button{width:16px;height:13px;cursor:pointer}

